/**
 * 1. Remove grey background on active links in IE 10.
 */
a {
  background-color: transparent; /* 1 */
  text-decoration: none;
  text-decoration-color: color($color-link a(33%));
  color: $color-link;
  transition: all 0.2s ease;
}

/**
 * 1. Improve readability when focused and also mouse hovered in all browsers
 */
a:active,
a:hover {
  color: $color-link--hover;
  text-decoration-color: color($color-link--hover a(33%));
  outline: 0; /* 1 */
}

a:active {
  color: $color-link--active;
  text-decoration-color: color($color-link--active a(33%));
}

@media print {
  a,
  a:visited {
    color: black !important;
  }
}
